@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600&display=swap);
header {
    height: 100vh;
    padding-top: 8rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
    justify-content: center;
}

/*==================HEADER SOCIALS=============================*/
.header__socials {
    display:flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    bottom: 3rem;
    justify-content:center;
    margin-top: 0.5rem;
}

.header__socials a {
    font-size: 2rem;
}


nav {
    background: rgba(0,0,0, 0.1);
    width: -webkit-max-content;
    width: max-content;
    padding: 0.8rem 1.8rem;
    display: block;
    z-index: 3;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display:flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    border-radius: 3rem;
    align-items: center;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
}

nav a{
    background: transparent;
    font-size: 1.2rem;
    padding: 1rem;
    border-radius: 50%;
}

.about-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

#about > h2 {
    margin: 20px auto 7rem auto;
}

.my-image {
    width: 200px;
    height:200px;
}

.my-image > img{
    border-radius: 1.5rem;
    margin: auto 0;
}

.about-content {
    width: 500px;
    text-align: justify;
    color: var(--color-white);
    font-size: 1.2rem;
}

.separator {
    border: 3px solid var(--color-primary);
    height: 220px;
    margin: auto 20px;
}

#about {
    overflow:auto;
}
#experiences > h2 {
   padding-top: 1rem;
   padding-bottom: 2rem;
}

.experience-container{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.container-expcard {
    width: 250px;
    height:300px;
    background-color: var(--color-bg);
    border-radius:10px;
    padding: 10px;
    overflow: hidden;
    margin: 20px;
    padding-bottom: 10px;
}

 .image-expcard {
   width: 100%;
   height: 65%;
   overflow: hidden;
   border-radius: 10px;
 }

 .details-exp > h4{
    color: var(--color-primary);
    font-weight: bold;
    text-align:center;
 }

 .details-exp {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
 }

 .used {
    color: var(--color-primary);
 }
.project-container {
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    align-items: flex-start;
}

#projects{
    height:100%;
}
.container-projcard {
    width: 300px;
    display: flex;
    flex-direction: column;
    background-color: var(--color-bg);
    border-radius:10px;
    padding: 20px;
    margin: 10px 10px;
}

.details-proj > p {
    text-align: center;
    font-size: 12px;

}

.details-proj > h4,h5 {
    text-align: center;
    font-weight: bold;
}

.image-projcard > img{
    border-radius: 10px;
}

.details-proj > h4{
    margin-top: 10px;
}

.footer-container {
    display: flex;
    align-items: center;
    justify-content:center;
    width: 100%;
    height: 2rem;
    background-color: var(--color-primary);
    margin-top: 45px;
}

.footer-container > h5 {
    color: var(--color-bg);
    font-weight: bold;
    font-size: 1rem;
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #0B0C10;
    --color-bg-alt: #1F2833;
    --color-primary: #2E8D36;
    --color-primary-alt: #0D5B28;
    --color-bone: #E4E3E1;
    --color-light: #86C232;
    --color-white: #FFF;

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
    background: #0B0C10;
    background: var(--color-bg);
    color: #FFF;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(/my-portfolio/static/media/texture-dark-wood.e3577ddd.png);
}

body::-webkit-scrollbar{
    display:none;
}

.App-main {
    display:flex;
    flex-flow: column wrap;
}

/* ======================GENERAL STYLES=================*/

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;;
}

h1,h2,h3,h4,h5 {
    font-weight: 500;
}

h1{
    font-size: 3.5rem;
}

section{
    margin-top: 8rem;
    height: 100vh;
    overflow: visible;
    padding-bottom: 2rem;
}

section > h2, section> h1{
    text-align: center;
    color: #0D5B28;
    color: var(--color-primary-alt);
}

section>h2{
    color: #2E8D36;
    color: var(--color-primary);
    margin-bottom: 2rem;
    font-weight:600;
}

.text-light{
    color: #0D5B28;
    color: var(--color-primary-alt);
}

a{
    color: #2E8D36;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #FFF;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #2E8D36;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #2E8D36;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #FFF;
    background: var(--color-white);
    color: #0B0C10;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #2E8D36;
    background: var(--color-primary);
    color: #0B0C10;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ===================MEDIA QUERIES (MEDIUM DEVICES) ==========*/
@media screen and (max-width:1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ===================MEDIA QUERIES (SMALL DEVICES) ==========*/
@media screen and (max-width:600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}
